.container-work {
    width: 75em;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url('../img/nasa.jpg');
    background-position: center;
    /* Center the image */
    background-repeat: no-repeat;
    /* Do not repeat the image */
    background-size: cover;
}

.show-nav-work {
    position: absolute;
    right: 68%;
    top: 5%;
    display: none;
    background: transparent;
    width: 2em;
    border: 0;
    font-size: 22px;
    padding: 0;
    outline: none;
    cursor: pointer;
}

.title-work {
    width: 100%;
    position: relative;
    bottom: 18%;
    font-size: 30px;
}

.title-work h4 {
    margin-left: 11%;
    font-family: 'Martel', serif;
}

.container-image {
    display: flex;
    width: 100%;
    position: relative;
    bottom: 10%;
}

.weather {
    width: 60%;
    margin-right: 10px;
    margin-left: 10px;
}

.tip-calculator {
    width: 60%;
    margin-right: 10px;
}

.count-down {
    width: 60%;
    margin-right: 10px;
}

.container-image-down {
    display: flex;
    width: 100%;
}

.exact-time {
    width: 60%;
    margin-right: 10px;
    margin-left: 10px;
}

.audi {
    width: 60%;
    margin-right: 10px;
}

.to-do {
    width: 60%;
    margin-right: 10px;
}

.weather img,
.tip-calculator img,
.count-down img,
.exact-time img,
.audi img,
.to-do img {
    width: 100%;
    transition: all .2s ease-in-out;
    border-radius: 8px;
}

.weather img:hover,
.tip-calculator img:hover,
.count-down img:hover,
.exact-time img:hover,
.audi img:hover,
.to-do img:hover {
    transform: scale(1.1);
}

@media only screen and (max-width: 600px) {
    .show-nav-work {
        display: flex;
        right: 82%;
        top: 9%;
    }

    .container-work {
        overflow: visible;
        width: 100%;
        height: fit-content;

    }

    .title-work {
        bottom: 0;
        width: 50%;
        margin-top: 81px;
    }

    .container-image {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        bottom: 0;
    }

    .weather,
    .tip-calculator,
    .count-down {
        margin-left: 0;
        margin-bottom: 15px;
        width: 85%;
    }

    .container-image-down {
        flex-direction: column;
        align-items: center;
    }

    .exact-time,
    .audi,
    .to-do {
        margin-left: 0;
        margin-bottom: 15px;
        width: 85%;
    }
}

/* iphone 5 */
@media only screen and (min-width: 320px) and (max-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
    .title-work {
        width: 51%;
    }
}