.nav {
    background: #f2f3f7;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-around;
    width: 17%;
    margin-right: 15px;
    height: 100%;
}


.profile-image {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profile-image img {
    border-radius: 50%;
    width: 60%;
    height: 10em;
}

.profile-image h2 {
    color: #000;
    font-size: 20px;
}

.profile-image p {
    font-size: 14px;
    color: #525253;
}

.link-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 50%;
    padding: 0;
}


.link-nav li {
    text-decoration: none;
    color: #5a5a5c;
    font-size: 14px;
    display: inline-block;
}

.link-nav li::after {
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background: #000;
    transition: width 0.3s;
}

.link-nav li:hover::after {
    width: 100%;
    /*transition: width .3s; */
}

.link-nav a {
    text-decoration: none;
}

.active-link {
    color: blue;
}

@media only screen and (max-width: 600px) {
    .nav {
        display: none;
        position: absolute;
        z-index: 1;
        width: 63%;
        left: 0;
    }

    .profile-image img {
        width: 59%;
        height: 8em;
    }
}

/* iphone 5 */
@media only screen and (min-width: 320px) and (max-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
    footer {
        font-size: 12px;

    }
}