.container-services {
  width: 75em;
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.show-nav-service {
  position: relative;
  top: 4%;
  width: 2em;
  display: none;
  border: 0;
  font-size: 22px;
  padding: 0;
  outline: none;
  cursor: pointer;
}

.title-services {
  width: 100%;
  position: relative;
  top: 4%;
}

.title-services h4 {
  position: relative;
  left: 17%;
  font-size: 20px;
  animation: fadeIn ease 3s;
  -webkit-animation: fadeIn ease 3s;
  -moz-animation: fadeIn ease 3s;
  -o-animation: fadeIn ease 3s;
  -ms-animation: fadeIn ease 3s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.container-box {
  width: 100%;
  position: relative;
  top: 13%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  -webkit-animation-name: fadeInLeft;
  -moz-animation-name: fadeInLeft;
  -o-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.7s;
  -moz-animation-duration: 0.7s;
  -o-animation-duration: 0.7s;
  animation-duration: 0.7s;
  -webkit-animation-delay: 0.7s;
  -moz-animation-delay: 0.7s;
  -o-animation-duration: 0.7s;
  animation-delay: 0.7s;
}

/** fadeInLeft **/

@-webkit-keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translatex(-10px);
    -moz-transform: translatex(-10px);
    -o-transform: translatex(-10px);
    transform: translatex(-10px);
  }

  to {
    opacity: 1;
    -webkit-transform: translatex(0);
    -moz-transform: translatex(0);
    -o-transform: translatex(0);
    transform: translatex(0);
  }
}

@-moz-keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translatex(-10px);
    -moz-transform: translatex(-10px);
    -o-transform: translatex(-10px);
    transform: translatex(-10px);
  }

  to {
    opacity: 1;
    -webkit-transform: translatex(0);
    -moz-transform: translatex(0);
    -o-transform: translatex(0);
    transform: translatex(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translatex(-100px);
    -moz-transform: translatex(-100px);
    -o-transform: translatex(-100px);
    transform: translatex(-100px);
  }

  to {
    opacity: 1;
    -webkit-transform: translatex(0);
    -moz-transform: translatex(0);
    -o-transform: translatex(0);
    transform: translatex(0);
  }
}

.container-box i {
  font-size: 28px;
}

.box1,
.box2,
.box3 {
  background: #f2f3f7;
  margin-left: 10px;
  border-bottom-style: solid;
  height: 13em;
  width: 28%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Martel", serif;
}

.box1 {
  border-bottom-color: coral;
}

.logo1 {
  height: 77px;
  position: absolute;
  border-radius: 50%;
  background: coral;
  right: 91%;
  bottom: 77%;
  width: 77px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo2 {
  height: 77px;
  position: absolute;
  border-radius: 50%;
  background: rgb(7, 110, 128);
  right: 57%;
  bottom: 77%;
  width: 77px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo3 {
  height: 77px;
  position: absolute;
  border-radius: 50%;
  background: rgb(133, 80, 255);
  right: 24%;
  bottom: 77%;
  width: 77px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box2 {
  border-bottom-color: rgb(7, 110, 128);
}

.box3 {
  border-bottom-color: rgb(133, 80, 255);
}

.container-animation {
  width: 95%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 19%;
  left: 3%;
  height: 45%;
  background: url("../img/back-rotation.jpeg");
  -webkit-animation-name: bottomFadeInLeft;
  -moz-animation-name: bottomFadeInLeft;
  -o-animation-name: bottomFadeInLeft;
  animation-name: bottomFadeInLeft;
  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.7s;
  -moz-animation-duration: 0.7s;
  -o-animation-duration: 0.7s;
  animation-duration: 0.7s;
  -webkit-animation-delay: 0.7s;
  -moz-animation-delay: 0.7s;
  -o-animation-duration: 0.7s;
  animation-delay: 0.7s;
}

/** fadeInLeft **/

@-webkit-keyframes bottomFadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translatex(-10px);
    -moz-transform: translatex(-10px);
    -o-transform: translatex(-10px);
    transform: translatex(-10px);
  }

  to {
    opacity: 1;
    -webkit-transform: translatex(0);
    -moz-transform: translatex(0);
    -o-transform: translatex(0);
    transform: translatex(0);
  }
}

@-moz-keyframes bottomFadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translatex(10px);
    -moz-transform: translatex(10px);
    -o-transform: translatex(10px);
    transform: translatex(10px);
  }

  to {
    opacity: 1;
    -webkit-transform: translatex(0);
    -moz-transform: translatex(0);
    -o-transform: translatex(0);
    transform: translatex(0);
  }
}

@keyframes bottomFadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translatex(100px);
    -moz-transform: translatex(100px);
    -o-transform: translatex(100px);
    transform: translatex(100px);
  }

  to {
    opacity: 1;
    -webkit-transform: translatex(0);
    -moz-transform: translatex(0);
    -o-transform: translatex(0);
    transform: translatex(0);
  }
}

.background {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(24, 24, 24, 0.95);
}

.icon-center {
  background: rgb(4, 3, 4);
  color: #fff;
  border-radius: 50%;
  width: 6%;
  height: 17%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}

.icon-center i {
  font-size: 28px;
}

.container-circle,
.container-circle2 {
  font-size: 27px;
  border-radius: 50%;
  margin: 3px;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.077);
  background: transparent;
}

.container-circle {
  font-size: 27px;
  position: relative;
  left: 15%;
  top: 16%;
  height: 7em;
  width: 67%;
}

.container-circle:hover,
.container-circle2:hover {
  border-color: rgba(255, 255, 255, 0.262);
}

.container-circle2 {
  width: 11em;
  height: 11em;
  animation: spin 23s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.javascript-icon,
.css-icon,
.react-icon {
  border-radius: 50%;
  width: 22%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 22%;
  background: #fff;
}

.html-icon,
.npm-icon,
.git-icon,
.adobe-icon {
  border-radius: 50%;
  width: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 13%;
  background: #fff;
}

.html-icon {
  position: absolute;
  width: 13%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

.git-icon {
  position: absolute;
  width: 13%;
  top: 88%;
  left: 66%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

.adobe-icon {
  position: absolute;
  width: 13%;
  left: 87%;
  top: 20%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

.npm-icon {
  position: absolute;
  width: 13%;
  top: 17%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

.javascript-icon {
  position: absolute;
  width: 21%;
  bottom: 45%;
  left: 89%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

.css-icon {
  position: absolute;
  width: 21%;
  right: 39%;
  top: 90%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

.react-icon {
  position: absolute;
  width: 21%;
  bottom: 82%;
  left: 12%;
  transform: rotate(0);
  animation: inverse 23s linear infinite;
}

/* Keyframe for each icon */

@keyframes inverse {
  0% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0);
  }
}

@media only screen and (max-width: 600px) {
  .show-nav-service {
    display: flex;
    left: 4%;
    background: transparent;
  }

  .container-services {
    overflow: visible;
    width: 100%;
    height: 100%;
  }

  .title-services {
    display: flex;
    justify-content: center;
  }

  .title-services h4 {
    left: 0;
  }

  .logo1,
  .logo2,
  .logo3 {
    height: 50px;
    width: 56px;
    right: auto;
  }

  .logo1 {
    bottom: 98%;
  }

  .logo2 {
    bottom: 65%;
  }

  .logo3 {
    bottom: 31%;
  }

  .container-box {
    flex-direction: column;
  }

  .box1,
  .box2,
  .box3 {
    width: 90%;
    margin-left: 0;
    justify-content: center;
    margin-bottom: 45px;
  }

  .container-animation {
    top: 12%;
    height: 28em;
    margin-bottom: 10px;
  }

  .container-circle {
    left: 16%;
    top: 16%;
    height: 61%;
    width: 61%;
  }

  .container-circle2 {
    width: 52%;
    height: 41%;
  }

  .icon-center {
    width: 15%;
    height: 12%;
  }

  .javascript-icon,
  .react-icon,
  .css-icon {
    width: 29%;
    height: 28%;
  }

  .html-icon,
  .npm-icon,
  .git-icon,
  .adobe-icon {
    width: 17%;
    height: 16%;
  }
}


/* iphone 5 */
@media only screen and (min-width: 320px) and (max-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
  .container-circle2 {
    width: 61%;
  }

  .icon-center {
    width: 18%;
  }
}