.container-contact {
    width: 75em;
    height: 100%;
    display: flex;
    overflow: hidden;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background: url('../img/contact-back3.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.show-nav-contact {
    position: absolute;
    right: 93%;
    display: none;
    top: 6%;
    background: transparent;
    width: 2em;
    border: 0;
    font-size: 22px;
    padding: 0;
    outline: none;
    cursor: pointer;
}

.title-contact {
    width: 89%;
    position: relative;
    bottom: 30%;
    font-size: 25px;
    color: #fff;
    text-shadow: 2px 2px 2px #000000;
    margin-left: 11%;
    font-family: 'Martel', serif;
}

.contact-method {
    width: 220px;
    height: 140px;
    display: inline-block;
    background: #3498db;
    text-align: center;
    margin: 10px;
    color: #fff;
    position: relative;
    cursor: pointer;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    padding: 0 10px;
}

.contact-method i {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    font-size: 30px;
    line-height: 140px;
    background: #34495e;
    z-index: 1;
    transition: transform 0.6s;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

.contact-method span {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 10px 0;
}

.contact-method:hover i {
    transform: translateY(-40px);
}

.social {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 13%;
}

.container-social a {
    color: #fff;
    font-size: 50px;
    margin-left: 40px;
    transition: color 0.5s;
}

.container-social i {
    text-shadow: 2px 2px 2px #000000;
}

.container-social a:hover {
    color: #3498db;
}


@media only screen and (max-width: 600px) {
    .show-nav-contact {
        display: flex;
        right: 82%;
    }

    .title-contact {
        bottom: 0;
        top: 4%;
        font-size: 20px;
        width: 100%;
        display: flex;
        justify-content: center;
        margin-left: 0;
    }

    .container-information {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: 64%;
        position: relative;
        top: 4%;
    }

    .social {
        top: 7%;
        justify-content: space-evenly;
    }

    .social a {
        margin-left: 0;
    }
}

/* iphone 5 */
@media only screen and (min-width: 320px) and (max-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {

    .title-contact {
        width: 66%;
        font-size: 17px;
    }

    .container-information {
        height: 60%;
    }

    .title-contact {
        height: 76px;

    }
}