.container-about {
    background: #fff;
    width: 75em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.show-nav-about {
    position: relative;
    display: none;
    right: 47%;
    top: 8%;
    width: 2em;
    border: 0;
    font-size: 22px;
    padding: 0;
    outline: none;
    cursor: pointer;
}

.title {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    left: 12%;
}

.title h6 {
    font-size: 18px;
    margin-bottom: 21px;
    font-family: 'Martel', serif;
}

.title h4 {
    font-size: 22px;
    margin-top: 2px;
    font-family: 'Martel', serif;
}

.description-life {
    text-align: justify;
    display: flex;
    justify-content: center;
    width: 90%;
    background: #F2F3F7;
    border-radius: 25px;
    -webkit-transition-duration: 0.4s;
    /* Safari */
    transition-duration: 0.4s;
}

.title,
.description-life,
.inner-width {
    animation-duration: 1s;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    opacity: 0;
    animation-name: fadeInUp;
    -webkit-animation-name: fadeInUp;
}

/* Animation */

@keyframes fadeInUp {
    from {
        transform: translate3d(0, 40px, 0)
    }

    to {
        transform: translate3d(0, 0, 0);
        opacity: 1
    }
}

@-webkit-keyframes fadeInUp {
    from {
        transform: translate3d(0, 40px, 0)
    }

    to {
        transform: translate3d(0, 0, 0);
        opacity: 1
    }
}


.description-life:hover {
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.description-life p {
    width: 85%;
    line-height: 35px;
    font-family: 'Martel', serif;
}

.middle {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.counting-sec {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 6em;
}

.inner-width {
    width: 90%;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 9em;
    text-align: center;
    background: #F2F3F7;
}

.col {
    width: 23%;
    line-height: 36px;
    font-size: 21px;
    transition: all .2s ease-in-out;
}

.col:hover {
    transform: scale(1.1);
}



.col i {
    font-size: 40px;
}


@media only screen and (max-width: 600px) {
    .show-nav-about {
        display: flex;
        position: relative;
        right: 38%;
        background: transparent;
        top: 4%;
    }

    .title {
        left: 0;
        top: 8%;
        align-items: center;
    }

    .counting-sec {
        margin-top: 2em;
    }

    .inner-width {
        height: 11em;
        align-items: flex-start;

    }

    .col {
        width: 100%;
        padding: 0;
        margin-top: 14px;
        font-size: 15px;
    }

    .description-life {
        position: relative;
        top: 13%;
    }

    .middle {
        position: relative;
        top: 13%;
    }
}


/* iphone 5 */
@media only screen and (min-width: 320px) and (max-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
    .description-life {
        top: 0;
    }

    .title {
        top: 0;
        width: 41%;
    }
}