/*Container */
.cover {
    height: 100%;
}

.show-nav-home {
    position: absolute;
    z-index: 1;
    background: transparent;
    top: 5%;
    left: 5%;
    display: none;
    width: 2em;
    border: 0;
    font-size: 22px;
    padding: 0;
    outline: none;
    cursor: pointer;
}

.container-elements {
    height: 100%;
}

.container-elements img {
    height: 100%;
    width: 75em;
    opacity: 0.9;
}

.description {
    position: absolute;
    top: 40%;
    left: 33%;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.description h2,
.description p {
    font-family: "Martel", serif;
    border-right: 0.15em solid orange;
    color: #fff;
    line-height: 43px;
    white-space: nowrap;
    overflow: hidden;
    font-size: 23px;
    text-align: center;
    text-shadow: 2px 2px 2px #000000;
}

.description h2 {
    font-size: 35px;
}

.description h2 {
    width: 13.3em;
    font-size: 25px;
    animation: type 2s steps(40, end);
    animation-fill-mode: forwards;
}

.description p:nth-child(2) {
    width: 17.5em;
    opacity: 0;
    animation: type2 2s steps(40, end);
    animation-delay: 2s;
    animation-fill-mode: forwards;
}

.description p:nth-child(3) {
    width: 25.3em;
    opacity: 0;
    font-size: 23px;
    animation: type3 5s steps(20, end), blink 0.5s step-end infinite alternate;
    animation-delay: 4s;
    animation-fill-mode: forwards;
}

@keyframes type {
    0% {
        width: 0;
    }

    99.9% {
        border-right: 0.15em solid orange;
    }

    100% {
        border: none;
    }
}

@-webkit-keyframes type {
    0% {
        width: 0;
    }

    99.9% {
        border-right: 0.15em solid orange;
    }

    100% {
        border: none;
    }
}

@keyframes type2 {
    0% {
        width: 0;
    }

    1% {
        opacity: 1;
    }

    99.9% {
        border-right: 0.15em solid orange;
    }

    100% {
        opacity: 1;
        border: none;
    }
}

@-webkit-keyframes type2 {
    0% {
        width: 0;
    }

    1% {
        opacity: 1;
    }

    99.9% {
        border-right: 0.15em solid orange;
    }

    100% {
        opacity: 1;
        border: none;
    }
}

@keyframes type3 {
    0% {
        width: 0;
    }

    1% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes type3 {
    0% {
        width: 0;
    }

    1% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes blink {
    50% {
        border-color: transparent;
    }
}

@-webkit-keyframes blink {
    50% {
        border-color: tranparent;
    }
}

@media only screen and (max-width: 600px) {

    #root {
        width: 100%;
    }

    .show-nav-home {
        display: flex;
        cursor: pointer;
    }

    .container {
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
    }

    .container-elements {
        overflow: hidden;
    }

    .cover {
        height: 100%;
        width: 100%;
    }

    .description {
        width: 100%;
        left: 0;
    }

    .description p:nth-child(2) {
        font-size: 20px;
    }

    .description p:nth-child(3) {
        font-size: 17px;
    }

    .cover img {
        width: 44em;
    }

    .close {
        display: none;
    }
}

/* iphone 5 */
@media only screen and (min-width: 320px) and (max-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
    .description p:nth-child(2) {
        font-size: 18px;
    }

    .description p:nth-child(3) {
        font-size: 14px;
    }
}