.container-skill {
    width: 75em;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url('../img/background-code.jpg');
    background-position: center;
    /* Center the image */
    background-repeat: no-repeat;
    /* Do not repeat the image */
    background-size: cover;
    /* Resize the background image to cover the entire container */
}

.show-nav-skills {
    position: relative;
    right: 47%;
    bottom: 21%;
    width: 2em;
    background: transparent;
    color: #fff;
    border: 0;
    display: none;
    font-size: 22px;
    padding: 0;
    outline: none;
    cursor: pointer;
}

.title-skills {
    font-size: 30px;
    position: relative;
    width: 89%;
    bottom: 25%;
    color: #fff;
    margin-left: 11%;
    font-family: 'Martel', serif;
}


.container-progress {
    background: #343334;
    width: 50%;
    height: 38%;
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}


.detail {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    height: 40px;
    margin-top: 1%;
}

.first {
    margin-left: 4%;
    color: #B8BFD6;
}

.detail p {
    margin-right: 4%;
    color: #B8BFD6;
}

.container-bar {
    width: 91%;
    height: 4%;
    border-radius: 3px;
    display: flex;
    align-items: center;
    border: 2px solid #457768;
}

.progress-bar-html {
    background: #0FFDB6;
    width: 80%;
    height: 80%;
    border-radius: 3px;
}

.progress-bar-css {
    background: #0FFDB6;
    width: 70%;
    height: 80%;
    border-radius: 3px;
}

.progress-bar-js {
    background: #0FFDB6;
    width: 65%;
    height: 80%;
    border-radius: 3px;
}

.progress-bar-react {
    background: #0FFDB6;
    width: 50%;
    height: 80%;
    border-radius: 3px;
}

.progress-bar-photoshop {
    background: #0FFDB6;
    width: 75%;
    height: 80%;
    border-radius: 3px;
}

@media only screen and (max-width: 600px) {
    .show-nav-skills {
        display: flex;
        justify-content: center;
        right: 42%
    }

    .title-skills {
        display: flex;
        justify-content: center;
        margin-left: 0;
        bottom: 16%;
    }

    .container-progress {
        width: 83%;
        height: 43%;
    }
}